import { Route, Routes } from "react-router-dom";
import { Layout } from "main/components";
import Home from "./home";
import LandingLayout from "main/views/landing/landing-layout";
import IndividualsFamily from "./individuals-family";
import JoinStaff from "./join-staff";
import Blog from "./blog";
import Facility from "./facility";
import Terms from "./terms";

const LandingRoute = () => {
  return (
    <Routes>
      <Route path='/'>
        <Route
          path='/'
          element={
            <LandingLayout>
              <Home />
            </LandingLayout>
          }
        />
        <Route
          path='/terms'
          element={
            <LandingLayout>
              <Terms />
            </LandingLayout>
          }
        />
        <Route
          path='/individuals-family'
          element={
            <LandingLayout>
              <IndividualsFamily />
            </LandingLayout>
          }
        />
        <Route
          path='/join-staff'
          element={
            <LandingLayout>
              <JoinStaff />
            </LandingLayout>
          }
        />
        <Route
          path='/blog'
          element={
            <LandingLayout>
              <Blog />
            </LandingLayout>
          }
        />
        <Route
          path='/facility'
          element={
            <LandingLayout>
              <Facility />
            </LandingLayout>
          }
        />
      </Route>
    </Routes>
  );
};

export default LandingRoute;
