import {
  Container,
  Heading,
  Stack,
  Text,
  Image,
  HStack,
  VStack,
} from "@chakra-ui/react";

const Blog = () => {
  return (
    <>
      <Container
        as={Stack}
        maxW={"6xl"}
        centerContent
        gap={5}
        py={{ base: 5, md: 10 }}
        mt={{ base: 5, md: 14 }}
      >
        <Heading
          fontSize={{ base: "xl", md: "2xl", lg: "3xl" }}
          maxW={"5xl"}
          textAlign='center'
        >
          The Necessity of a Digital/automated Scheduling Platform to Manage
          Shortage of Nurses
        </Heading>
        <Text color='GrayText'>November 10, 2022</Text>
        <Image src='/assets/images/blog1.png' rounded='lg' my={5} />
        <VStack alignItems='left' gap={8}>
          <Text>
            It is almost impossible to read the news without coming across a
            lead story cataloguing the latest cyber breach or misuse of data.
            Intellectual property is being stolen from companies at an alarming
            rate. Foreign actors are meddling in elections. Criminals use the
            dark recesses of the internet to sell drugs, guns and even people.
            The volume of these events lays bare the paradox of the digital
            economy and cyber security. On one hand, technology has led to
            convenience, efficiency and wealth creation. On the other hand, this
            great push to digitize society has meant building inherent
            vulnerability into the core of the economic taking place atop a
            deeply fragmented and underdeveloped system of global rules.
          </Text>
          <Heading textAlign='left'>
            Improved communication between nurses and their managers.
          </Heading>
          <Text>
            It is critical for managers to have an understanding of your nurse's
            challenges. You may think you do, but chances are that you don't
            understand the full extent of what they experience daily. If your
            nurses feel they can't count on you to be there for them, they won't
            want to work for you anymore. That would be a problem with no easy
            solution! That said, there are some ways managers can improve
            communication with their employees (and vice versa). One thing we've
            found helpful is by creating regular check-in meetings between
            managers and their teams. They're not required meetings; instead,
            they're just something we do every once in a while, so everyone
            knows where things stand with each other and how everyone's doing
            emotionally/mentally/physically.
          </Text>
          <Heading textAlign='left'>
            Guaranteed better staff organization, so hospitals can schedule
            nurses based on their strengths and experience.
          </Heading>
          <Text>
            The benefits of an effective and efficient scheduling platform are
            apparent. The more accurate your schedule, the better you can use
            your staff's time and resources. This means you'll be able to
            organize shifts more efficiently and ensure that nurses are in the
            right place at the right time. Furthermore, a digital platform helps
            with work-life balance for nursing staff by ensuring consistent
            hours so they know when to plan their lives around work commitments
            like family events or to socialize with friends outside of work
            hours.
            <br /> Better staff organization also means a more stable staffing
            level for hospital management because there are no unnecessary costs
            associated with overtime payouts from poor scheduling decisions
            (often necessary when dealing with short-staffing issues). In
            addition, it creates a better work-life balance for nurses.
            <br />
            As a nurse, you're probably familiar with the following scenario:
            you are working in a hospital with an overabundance of patients and
            not enough nurses to provide adequate care. Most people think this
            is an issue that only affects hospitals, but it's a problem we see
            throughout many industries - schools, restaurants, hotels etc. In
            fact, experts estimate that $6 billion could be saved per year if
            businesses used better scheduling software.
          </Text>
          <Image src='/assets/images/blog2.png' rounded='lg' my={5} />
          <Heading textAlign='left'>
            Digital platforms for nurse management
          </Heading>
          <Text>
            Enables more technology in healthcare and increased sharing of data
            between departments. In the healthcare industry, more technology is
            needed to help manage the shortage of nurses. A solution that could
            solve this problem is a scheduling digital platform that would
            create a schedule for each day, week, or month depending on how long
            the nurse works there. If they work only part-time, it automatically
            calculates how many hours they need to work in order to get paid for
            full-time hours. A scheduler can easily see what other staff members
            are working on when making changes or rescheduling staff members if
            someone calls in sick or doesn't show up for work at all.
            <br /> We hope this article has convinced you of the importance of a
            digital/automated scheduling platform in healthcare. Please contact
            us if you are interested in being part of our team!
          </Text>
        </VStack>
      </Container>
    </>
  );
};
export default Blog;
