import { ReactNode } from "react";

import {
  Box,
  Container,
  SimpleGrid,
  Stack,
  Text,
  useColorModeValue,
  Image,
  Link as ChakraLink,
  Heading,
  Flex,
  Divider,
  HStack,
  IconButton,
  Icon,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import {
  ImFacebook,
  ImInstagram,
  ImLinkedin,
  ImTelegram,
  ImTwitter,
} from "react-icons/im";

const ListHeader = ({ children }: { children: ReactNode }) => {
  return (
    <Heading size='md' mb={2}>
      {children}
    </Heading>
  );
};

export default function Footer() {
  return (
    <Box>
      <Container
        as={Stack}
        maxW={"7xl"}
        py={10}
        px={5}
        gap={10}
        alignItems={{ base: "center", md: "start" }}
      >
        <Flex
          flexDir={{ base: "column", md: "row" }}
          gap={8}
          alignItems={{ base: "center", md: "start" }}
        >
          <Image
            src='/assets/images/Logo.png'
            height='24'
            width='24'
            objectFit='contain'
          />
          <SimpleGrid columns={{ base: 1, md: 2, lg: 4 }} spacing={8}>
            <Stack align={"flex-start"}>
              <ListHeader>Address</ListHeader>
              <ChakraLink
                _hover={{ textDecoration: "none" }}
                href='tel:(909) 999-1241'
              >
                4193 Flat Rock Dr, Suite 200, Office 484 Riverside, CA 92505
              </ChakraLink>
            </Stack>
            <Stack align={"flex-start"}>
              <ListHeader>Contact</ListHeader>

              <ChakraLink
                _hover={{ textDecoration: "none" }}
                href='tel:(909) 999-1241'
              >
                (909) 999-1241
              </ChakraLink>

              <ChakraLink
                _hover={{ textDecoration: "none" }}
                href='mailto:inlandempirehomecare@gmail.com'
              >
                inlandempirehomecare@gmail.com
              </ChakraLink>

              <ChakraLink
                _hover={{ textDecoration: "none" }}
                href='mailto:info@nursebillboard.com'
              >
                info@nursebillboard.com
              </ChakraLink>
            </Stack>
            <Stack align={"flex-start"}>
              <ListHeader>Legal </ListHeader>
              <Link to={"#"}>Privacy Policy</Link>
              <Link to='/terms'>Term of use</Link>
            </Stack>
            <Stack align={"flex-start"}>
              <ListHeader>Company</ListHeader>
              <Link to='/'>How it works</Link>
              <Link to='/blog'>Blog</Link>
              <Link to={"#"}>Contact</Link>
            </Stack>
          </SimpleGrid>
        </Flex>
        <Divider
          bgColor='red.200'
          height={0.5}
          rounded='md'
          maxW={"7xl"}
          my={3}
          mx='auto'
        />
        <Flex
          justifyContent={{ base: "center", md: "space-between" }}
          flexDir={{ base: "column", md: "row" }}
          alignItems='center'
          gap={5}
          width='100%'
        >
          <Text textAlign={{ base: "center", md: "left" }}>
            2022 © All Rights Reserved
          </Text>
          <Flex
            justifyContent={{ base: "center", md: "space-between" }}
            flexDir={{ base: "column", md: "row" }}
            alignItems='center'
            gap={5}
          >
            <Text>Follow us :</Text>
            <HStack>
              <IconButton
                aria-label='social'
                rounded='full'
                bg='red.50'
                p={3}
                icon={<Icon as={ImFacebook} />}
              />
              <IconButton
                aria-label='social'
                rounded='full'
                bg='red.50'
                p={3}
                icon={<Icon as={ImInstagram} />}
              />
              <IconButton
                aria-label='social'
                rounded='full'
                bg='red.50'
                p={3}
                icon={<Icon as={ImTwitter} />}
              />
              <IconButton
                aria-label='social'
                rounded='full'
                bg='red.50'
                p={3}
                icon={<Icon as={ImTelegram} />}
              />
              <IconButton
                aria-label='social'
                rounded='full'
                bg='red.50'
                p={3}
                icon={<Icon as={ImLinkedin} />}
              />
            </HStack>
          </Flex>
        </Flex>
      </Container>
    </Box>
  );
}
