import { ReactNode, useState } from "react";
import {
  Box,
  Flex,
  Avatar,
  HStack,
  IconButton,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  Link as ChakraLink,
  useDisclosure,
  useColorModeValue,
  Stack,
  Image,
  FlexProps,
  Collapse,
  Heading,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  LinkProps,
  SimpleGrid,
  FormControl,
  FormErrorMessage,
  FormLabel,
  Input,
  VStack,
  Textarea,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon, AddIcon } from "@chakra-ui/icons";
import { NavItemType } from "main/configs/navigation.config";
import { Link, useLocation } from "react-router-dom";
import Footer from "./footer";
import { useForm, Controller } from "react-hook-form";
import { api } from "main/configs/axios.config";
import { toast } from "react-toastify";

const Links = [
  { name: "Home", icon: undefined, itemPath: "/" },
  {
    name: "Individuals/Family",
    icon: undefined,
    itemPath: "/individuals-family",
  },
  { name: "Facility", icon: undefined, itemPath: "/facility" },
  { name: "Join Our Staff", icon: undefined, itemPath: "/join-staff" },
  { name: "Blog", icon: undefined, itemPath: "/blog" },
  { name: "Contact", icon: undefined, itemPath: "#" },
];
type ContactForm = {
  first_name: string;
  last_name: string;
  company: string;
  email: string;
  message: string;
};
type NavLinkProps = {
  link: NavItemType;
} & LinkProps;
const NavLink = ({ link, ...props }: NavLinkProps) => {
  let pathname: string = useLocation().pathname;
  const isNavLinkActive = (itemPath: string) => {
    if (
      link.itemPath == "/"
        ? link.itemPath === pathname
        : pathname.includes(itemPath)
    ) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <ChakraLink
      {...props}
      onClick={props.onClick}
      px={2}
      py={1}
      rounded={"md"}
      color={isNavLinkActive(link.itemPath) ? "#E3022A" : "GrayText"}
      _hover={{
        textDecoration: "none",
        color: "#E3022A",
      }}
    >
      <Link to={link.itemPath}>
        <Heading size='sm' color='inherit'>
          {link.name}
        </Heading>
      </Link>
    </ChakraLink>
  );
};
type LandingLayoutProps = {
  children: ReactNode;
};
export default function LandingLayout(props: LandingLayoutProps) {
  const { children } = props;
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { control, formState, handleSubmit } = useForm<ContactForm>();
  const [openModal, setOpenModal] = useState<boolean>(false);
  let pathname: string = useLocation().pathname;
  const isNavLinkActive = (itemPath: string) => {
    if (itemPath == "/" ? itemPath === pathname : pathname.includes(itemPath)) {
      return true;
    } else {
      return false;
    }
  };
  const onSubmit = async (data: ContactForm) => {
    const response = await api.post('contact', undefined, { params: data });
    if (response) {
      toast.success("Message sent succesfully.")
      setOpenModal(false);
    }

  };
  return (
    <>
      <Box bg='#fff' px={{ base: 4, md: 15 }} boxShadow='md'>
        <Flex h={20} alignItems={"center"} justifyContent={"space-between"}>
          <Box>
            <Image
              src='/assets/images/Logo.png'
              height='16'
              width='16'
              objectFit='contain'
            />
          </Box>
          <HStack as={"nav"} spacing={4} display={{ base: "none", md: "flex" }}>
            {Links.map((link) => (
              <ChakraLink
                key={link.name}
                onClick={() => {
                  link.name === "Contact"
                    ? setOpenModal(true)
                    : setOpenModal(false);
                }}
                px={2}
                py={1}
                rounded={"md"}
                color={isNavLinkActive(link.itemPath) ? "#E3022A" : "GrayText"}
                _hover={{
                  textDecoration: "none",
                  color: "#E3022A",
                }}
              >
                <Link to={link.itemPath}>
                  <Heading size='sm' color='inherit'>
                    {link.name}
                  </Heading>
                </Link>
              </ChakraLink>
            ))}
          </HStack>
          <IconButton
            size={"md"}
            variant='ghost'
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <Actions display={{ base: "none", md: "block" }} />
        </Flex>
        <Collapse in={isOpen}>
          <Box pb={5}>
            <Stack as={"nav"} spacing={4}>
              {Links.map((link) => (
                <NavLink
                  key={link.name}
                  link={link}
                  onClick={() => {
                    link.name === "Contact" && setOpenModal(true);

                  }}
                />
              ))}
            </Stack>

            <Actions display={{ base: "block", md: "none" }} mt={5} />
          </Box>
        </Collapse>
      </Box>

      <Box>{children}</Box>
      <Footer />
      <Modal isOpen={openModal} onClose={() => setOpenModal(false)} size='4xl'>
        <ModalOverlay />
        <ModalContent mx={{ base: 5, md: 0 }}>
          <ModalCloseButton />
          <ModalBody mb={5}>
            <SimpleGrid columns={{ base: 1, md: 2 }} mt={8}>
              <form>
                <VStack p={5} gap={5}>
                  <Controller
                    control={control}
                    name='first_name'
                    rules={{ required: "Field is required." }}
                    render={({
                      field: { onChange, value },
                      fieldState: { error },
                    }) => (
                      <FormControl isInvalid={!!error} isRequired>
                        <FormLabel>First Name:</FormLabel>
                        <Input type='text' value={value} onChange={onChange} />
                        {error && (
                          <FormErrorMessage>{error.message}</FormErrorMessage>
                        )}
                      </FormControl>
                    )}
                  />
                  <Controller
                    control={control}
                    name='last_name'
                    rules={{ required: "Field is required." }}
                    render={({
                      field: { onChange, value },
                      fieldState: { error },
                    }) => (
                      <FormControl isInvalid={!!error} isRequired>
                        <FormLabel>Last Name:</FormLabel>
                        <Input type='text' value={value} onChange={onChange} />
                        {error && (
                          <FormErrorMessage>{error.message}</FormErrorMessage>
                        )}
                      </FormControl>
                    )}
                  />
                  <Controller
                    control={control}
                    name='company'
                    rules={{ required: "Field is required." }}
                    render={({
                      field: { onChange, value },
                      fieldState: { error },
                    }) => (
                      <FormControl isInvalid={!!error} isRequired>
                        <FormLabel>Company:</FormLabel>
                        <Input type='text' value={value} onChange={onChange} />
                        {error && (
                          <FormErrorMessage>{error.message}</FormErrorMessage>
                        )}
                      </FormControl>
                    )}
                  />
                  <Controller
                    control={control}
                    name='email'
                    rules={{ required: "Field is required." }}
                    render={({
                      field: { onChange, value },
                      fieldState: { error },
                    }) => (
                      <FormControl isInvalid={!!error} isRequired>
                        <FormLabel>Work email:</FormLabel>
                        <Input type='text' value={value} onChange={onChange} />
                        {error && (
                          <FormErrorMessage>{error.message}</FormErrorMessage>
                        )}
                      </FormControl>
                    )}
                  />
                  <Controller
                    control={control}
                    name='message'
                    rules={{ required: "Field is required." }}
                    render={({
                      field: { onChange, value },
                      fieldState: { error },
                    }) => (
                      <FormControl isInvalid={!!error} isRequired>
                        <FormLabel>How can we help you?:</FormLabel>
                        <Textarea cols={5} value={value} onChange={onChange} />
                        {error && (
                          <FormErrorMessage>{error.message}</FormErrorMessage>
                        )}
                      </FormControl>
                    )}
                  />
                  <Button
                    backgroundColor='#E3022A'
                    color={"#fff"}
                    variant='solid'
                    onClick={handleSubmit(onSubmit)}
                    isLoading={formState.isSubmitting}
                    marginTop={4}
                    width={"100%"}
                    sx={{ "&:hover": { backgroundColor: "#CC0024" } }}
                  >
                    Submit
                  </Button>
                </VStack>
              </form>
              <Box
                sx={{
                  backgroundColor: " #f42348",
                  paddingTop: "1rem",
                  backgroundImage: 'url("../assets/images/AppMonogram.png")',
                  backgroundPosition: "center",
                  backgroundRepeat: "no-repeat",
                  backgroundSize: "contain",
                }}
              ></Box>
            </SimpleGrid>
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
}
const Actions = (props: FlexProps) => {
  return (
    <Flex alignItems={"center"} {...props}>
      <Link to='/login'> <Button
        variant='outline'
        colorScheme='red'
        size='md'
        mr={4}
        borderRadius='full'
      >
        Login
      </Button></Link>
      <Link to='/register'> <Button
        variant='solid'
        colorScheme='red'
        size='md'
        mr={4}
        borderRadius='full'
      >
        Facility Register
      </Button></Link>
    </Flex>
  );
};
