import {
    Spinner, Flex
} from "@chakra-ui/react";
import { PDFDocument } from "pdf-lib";
import { useCallback, useState, useEffect } from "react";

const Terms = () => {
    const [frameBytes, setFrameBytes] = useState<any>();
    const readPdf = useCallback(async () => {
        const existingPdfBytes = await fetch('/terms.pdf').then((res) => res.arrayBuffer());
        const pdfDoc = await PDFDocument.load(existingPdfBytes);
        const frameToBytes = await pdfDoc.saveAsBase64({ dataUri: true });
        setFrameBytes(frameToBytes);
    }, []);

    useEffect(() => {
        readPdf();
    }, [readPdf]);
    return <Flex minH={"100vh"} w={{ base: '100%', md: '80%' }} mx='auto' my={2} justifyContent='center' rounded='md' alignItems='center' bg='#fff' p={5} flexDir='column'>{frameBytes ? < iframe id="contract" style={{ width: '100%', height: '60vh' }
    } src={frameBytes} ></iframe> : <Spinner color="red" />}</Flex>;
};
export default Terms;
